/**BODY**/
/**FUENTES**/
/**COLORS**/
header {
  background: #DF0209;
  border-bottom: 1px solid #FFED00;
  height: 70px;
  position: relative;
  font-family: "Lato-Regular";
}

header .header-first-row {
  display: none;
  height: 26px;
  padding: 0 17px;
  border-bottom: 1px solid #CCCCCC;
  font-size: 10px;
  line-height: 26px;
}

header .header-first-row .contacts-number {
  display: inline-block;
}

header .header-first-row .contacts-number .phone-title {
  color: #666666;
  margin: 0 3px;
}

header .header-first-row .contacts-number .phone-number {
  color: #DF0209;
  margin-right: 5px;
}

header .header-first-row .header-options {
  display: inline-block;
  float: right;
}

header .header-first-row .header-options a {
  color: #666666;
  padding-left: 30px;
}

header .header-first-row .header-options a span {
  padding-left: 4px;
}

header .header-second-row {
  position: relative;
  width: 100%;
  height: 100%;
  display: block;
}

header .header-second-row #search-container {
  display: block;
  position: absolute;
  bottom: 0;
  background: #f2f2f2;
  font-size: 12px;
  height: 30px;
  width: 100%;
  text-align: center;
}

header .header-second-row #search-container input[type="text"] {
  border: none;
  font-family: "Lato-Regular";
  text-align: center;
  height: 100%;
  width: 50%;
  background: #f2f2f2;
  letter-spacing: 1px;
  outline: 0;
  padding: 0;
}

header .header-second-row #search-container input[type="text"]::-webkit-input-placeholder {
  color: #333333;
}

header .header-second-row #search-container input[type="text"]::-moz-placeholder {
  color: #333333;
}

header .header-second-row #search-container input[type="text"]:-ms-input-placeholder {
  color: #333333;
}

header .header-second-row #search-container input[type="text"]:-moz-placeholder {
  color: #333333;
}

header .header-second-row #search-container .search-icon {
  margin-right: 5px;
}

header .header-second-row .container-logo-search {
  width: 100%;
}

