@media screen and (min-width: 767px) {
  footer .logo {
    padding-top: 24px;
  }
  footer .logo .footer-icon.footer-metro-icon {
    height: 100px;
    width: 100px;
  }
  footer .promesas-valor > a {
    width: 25%;
    border: 0;
  }
  footer .promesas-valor > a:nth-child(odd) {
    border: 0;
  }
  footer .newsletter {
    height: 160px;
    background: url("../../assets/img/newsletter-tablet.jpg") no-repeat center;
    background-size: cover;
    position: relative;
  }
  footer .newsletter .form-container {
    width: 50%;
    height: 90px;
    right: 0;
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
  }
  footer .newsletter .form-container .newsletter-header {
    width: 44%;
    display: inline-block;
    position: relative;
    text-align: left;
    padding-top: 0;
    vertical-align: top;
  }
  footer .newsletter .form-container .newsletter-header i {
    font-size: 29px;
    position: absolute;
    left: -36px;
  }
  footer .newsletter .form-container .newsletter-header span {
    display: block;
  }
  footer .newsletter .form-container #newsletter-form {
    display: inline-block;
    width: 55%;
    padding-right: 15px;
    box-sizing: border-box;
    vertical-align: top;
  }
  footer .static-links .links-container {
    display: inline-block;
    text-align: center;
    width: 45%;
    vertical-align: top;
  }
  footer .static-links .links-container:nth-child(1) {
    margin-right: 9%;
  }
  footer .static-links .links-container .links-section {
    display: inline-block;
    width: 48%;
    border: 0;
    vertical-align: top;
    box-sizing: border-box;
  }
  footer .static-links .links-container .links-section .links-header {
    text-align: left;
    padding: 9px 0 9px 15px;
  }
  footer .static-links .links-container .links-section .links-list {
    text-align: left;
    padding: 5px 15px;
  }
  footer .legals {
    height: 28px;
    line-height: 28px;
  }
  footer .legals div {
    display: inline-block;
  }
  footer .legals .pipeline {
    display: inline-block;
  }
}

@media screen and (min-width: 1349px) {
  footer .newsletter {
    background: url("../../assets/img/newsletter-desktop.jpg") no-repeat center;
    background-size: cover;
  }
  footer .newsletter .form-container {
    width: 45%;
    padding-right: 73px;
    box-sizing: border-box;
  }
  footer .newsletter .form-container .newsletter-header i {
    font-size: 35px;
    left: -42px;
  }
  footer .newsletter .form-container .newsletter-header h2 {
    font-size: 20px;
  }
  footer .newsletter .form-container .newsletter-header span {
    font-family: "Lato-Bold";
  }
  footer .static-links .links-container .links-section {
    padding: 0 5%;
  }
  footer .logo .footer-icon.footer-metro-icon {
    height: 130px;
    width: 130px;
  }
}
